import React, { useContext } from 'react';
import cn from 'classnames';
import { createPortal } from 'react-dom';
import { ModalContext } from '../../../constants/constants';
import { LayoutHooks } from '../../../hooks/LayoutHooks';
import styles from './Modal.module.css';

const Modal = ({ children, theme }) => {
  const setIsVisible = useContext(ModalContext);
  LayoutHooks.useLockBodyScroll();

  const content = <div className={styles.container}>
    <div className={styles.overlay} onClick={() => setIsVisible(false)} />
    <div className={cn(styles.modal, theme.modal)}>
      <button
        className={styles.modalClose}
        type="button"
        onClick={() => setIsVisible(false)}
      />
      <div className={cn(styles.modalBody, theme.modalBody)}>{children}</div>
    </div>
  </div>;

  return createPortal(content, document.getElementById('root-modal'));
};

export default Modal;